import React, { useState, useRef, useEffect, useMemo } from "react";
import { inject, observer } from "mobx-react";
import "./history.less";
import DeriNumberFormat from "../../../../utils/DeriNumberFormat";
import dateFormat from "date-format";
import classNames from "classnames";
import Icon from "../../../../components/Icon/Icon";
import { camelize } from "../../../../utils/utils";
import FixedColumn from "../FixedColumn/FixedColumn";
import GridHeader from "../GirdHeader/GridHeader";
import TradeMaskHint from "../../../../components/TradeMaskHint/TradeMaskHint"

function History({ lang, trading, wallet }) {
  const [bodyWidth, setBodyWidth] = useState(0);
  const headerRef = useRef();
  const columns = useMemo(
    () => ({
      time: {
        text: lang["time"],
        key: "time",
        format: (value, h) => {
          return (
            <div>
              {dateFormat.asString("yyyy-MM-dd hh:mm:ss", new Date(parseInt(value)))}&nbsp;&nbsp;
              <a target="_blank" rel="noreferrer" href={`${wallet.blockExploreUrl}/tx/${h.transactionHash}`}>
                <Icon token="scan" width="14" />
              </a>
            </div>
          );
        },
      },
      direction: { text: lang["direction"], key: "direction", format: (value) => (value === "LIQUIDATION" ? lang["liquidation"] : camelize(value)) },
      displaySymbol: { text: lang["symbol"], key: "displaySymbol", format: (value, h) => value || h["symbol"] },
      price: {
        text: lang["entry-price"],
        key: "price",
        tip: lang["entry-price-tip"],
        format: (value, h) => {
          return (
            <DeriNumberFormat value={value} decimalScale={trading.symbolInfo.getDecimalBySymbol && trading.symbolInfo.getDecimalBySymbol(h.symbol)} />
          );
        },
      },
      indexPrice: { text: lang["index-price"], tip: lang["index-price-tip"], key: "indexPrice", type: "number", decimal: 2 },
      volume: { text: lang["volume"], tip: lang["history-volume-tip"], key: "volume", type: "number" },
      notional: { text: lang["notional"], tip: lang["history-notional-tip"], key: "notional", type: "number", decimal: 2 },
      transactionFee: { text: lang["transaction-fee"], key: "transactionFee", type: "number", decimal: 2 },
    }),
    [trading.symbolInfo]
  );

  const keys = useMemo(() => Object.keys(columns), [columns]);

  useEffect(() => {
    const resizeHandler = () => {
      if (headerRef.current) {
        if (headerRef.current.scrollWidth === headerRef.current.clientWidth) {
          setBodyWidth(headerRef.current.parentElement.scrollWidth);
        } else {
          setBodyWidth(headerRef.current.scrollWidth);
        }
      }
    };
    setBodyWidth(headerRef.current.parentElement.scrollWidth);
    window.addEventListener("resize", resizeHandler);
    return () => window.removeEventListener("resize", resizeHandler);
  }, [trading.history]);

  return (
    <div className="his-container">
      <div className="m-history">
        <div className="h-header" ref={headerRef} style={{ width: `${bodyWidth}px` }}>
          <GridHeader columns={columns} keys={keys} className="h-col" />

          {/* {columns.map((col, index) => (
            <div key={index} className='h-col'>{col.text}</div>
          ))} */}
        </div>
        <div className="h-body" style={{ width: `${bodyWidth}px` }}>
          <TradeMaskHint postion="histroy" />
          <HistoryList history={trading.history} keys={keys} columns={columns} lang={lang} />
        </div>
      </div>
      {trading.loaded >= 4 && <FixedColumn data={trading.history} selector=".m-history" />}
    </div>
  );
}

const HistoryList = React.memo(({ history, keys, columns, lang }) => {
  return history ? (
    history.length > 0 ? (
      history.map((h, i) => (
        <div className="h-row" key={i}>
          {keys.map((col, index) => {
            const column = columns[col];
            const value = column.format ? column.format(h[column.key], h) : h[column.key];
            const clazz = classNames("h-col", [column.key]);
            return (
              <div className={clazz} key={index}>
                {column.type === "number" ? value === "-" ? "--" : <DeriNumberFormat value={value} decimalScale={column.decimal} /> : value}
              </div>
            );
          })}
        </div>
      ))
    ) : (
        <div className="no-data">
          <Icon token="no-data" width="48" height="48" />
          {lang["no-data"]}
        </div>
      )
  ) : (
      <>
        <div className="h-row loading-line"></div>
        <div className="h-row loading-line"></div>
        <div className="h-row loading-line"></div>
        <div className="h-row loading-line"></div>
        <div className="h-row loading-line"></div>
      </>
    );
});

// function HistoryList({ loaded, history, columns, lang }) {

// }
export default inject("trading", "wallet")(observer(History));
