import { React, useState, useEffect } from 'react'
import Icon from '../Icon/Icon'
import { hide } from 'react-functional-modal'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import { getDefaultNw } from '../../utils/utils'
import { DeriEnv } from '../../lib/web3js'
import ApiProxy from '../../model/ApiProxy'
import Type from '../../model/Type'
import Intl from '../../model/Intl'
import Chart from './Chart'
import './simulatePnl.less'
import DownLogo from '../../assets/img/pnlDown.svg'
import UpLogo from '../../assets/img/pnlUp.svg'
import NoVol from '../../assets/img/no-volatility.svg'
import longLogo from './img/long.svg'
import shortLogo from './img/short.svg'
const getLang = key => {
  return Intl.get('trade', key)
}
export default function SimulatePnl({ wallet, trading, lang }) {
  const indexName = trading.symbolInfo.isPower ? `${trading.symbolInfo.symbolWithoutPower} ${getLang('price')}` : trading.symbolInfo.isOption ? trading.symbolInfo.underlier : lang['index-price']
  const marketName = trading.symbolInfo.formatedSymbol
  const [data, setData] = useState(null)
  const [initData, setInitData] = useState(null)
  const [isLong, setIsLong] = useState(true)
  const [nowPnl, setNowPnl] = useState({
    indexPriceChange: 0,
    markPriceChange: 0
  })
  const [isIndexUp, setIsIndexUp] = useState(true)
  const [isMarkUp, setIsMarkUp] = useState(true)
  const [lastData, setLastData] = useState({})
  const load = async () => {
    if (trading.symbolInfo.address && trading.symbolInfo.symbol) {
      let res = await ApiProxy.request('getSimulatePnls', [wallet.detail.chainId || getDefaultNw(DeriEnv.get()).id, trading.symbolInfo.address, trading.symbolInfo.symbol])
      res = res.map((item) => {
        item.indexPriceChange = item.indexPriceChange * 100
        item.markPriceChange = item.markPriceChange * 100
        item.indexPrice = Number(item.indexPrice)
        item.markPrice = Number(item.markPrice)
        return item
      })
      if (res.length > 0) {
        setLastData(res[(res.length - 1) / 2])
      }
      setData(res)
      setInitData(res)
    }

  }
  const onChange = (props) => {
    if (props) {
      setNowPnl(props[0].payload)
    }
  }

  const tabLong = (bool) => {
    if (data && initData) {
      let arr = JSON.parse(JSON.stringify(initData))
      if (bool) {
        setData(arr)
        setIsLong(bool)
      } else {
        arr = arr.map((item) => {
          item.markPriceChange = -(item.markPriceChange)
          return item
        })
        setData(arr)
        setIsLong(bool)

      }
    }
  }

  useEffect(() => {
    load()
  }, [wallet, trading])

  useEffect(() => {
    if (nowPnl.indexPriceChange || nowPnl.markPriceChange) {
      let indexBool = nowPnl.indexPriceChange > 0 ? true : false
      let markBool = nowPnl.markPriceChange > 0 ? true : false
      setIsIndexUp(indexBool)
      setIsMarkUp(markBool)
    }
  }, [nowPnl])
  return (
    <div className='simulate-pnl-modal'>
      <div className='simulate-pnl-header'>
        <div className='simulate-title'>{lang['simulate-pnl']}</div>
        <div className='simulate-close'>
          <Icon token='close-modal-icon' width='15' height='16' onClick={() => { hide('simulatePnl') }} />
        </div>
      </div>
      <div className='simulate-pnl-body'>
        <div className='association-pnl'>
          <div className='index-symbol'>
            <div className='symbol-name'>
              {indexName}
            </div>
            <div className='symbol-price'>
              {nowPnl ? <DeriNumberFormat value={nowPnl.indexPrice} decimalScale={2} /> : <DeriNumberFormat value={trading.index} decimalScale={2} />}
              <span className={nowPnl.indexPriceChange == 0 ? 'm-quote-change zero' : isIndexUp ? 'm-quote-change' : 'm-quote-change down'}>
                <img src={nowPnl.indexPriceChange == 0 ? NoVol : isIndexUp ? UpLogo : DownLogo} />
                {nowPnl.indexPriceChange ? <DeriNumberFormat value={nowPnl.indexPriceChange} decimalScale={2} suffix='%' /> : '0%'}
              </span>
            </div>
            <div className={nowPnl.indexPriceChange == 0 ? 'quote-change zero' : isIndexUp ? 'quote-change' : 'quote-change down'}>
              <img src={nowPnl.indexPriceChange == 0 ? NoVol : isIndexUp ? UpLogo : DownLogo} />
              {nowPnl.indexPriceChange ? <DeriNumberFormat value={nowPnl.indexPriceChange} decimalScale={2} suffix='%' /> : '0%'}
            </div>
          </div>
          <div className='association-logo'>
            <Icon token='association' />
          </div>
          <div className='mark-symbol'>
            <div className='symbol-name'>
              {marketName}
            </div>
            <div className='symbol-price'>
              <span>
                {nowPnl ? <DeriNumberFormat value={nowPnl.markPrice} decimalScale={2} /> : <DeriNumberFormat value={trading.markPrice} decimalScale={2} />}
              </span>
              <div className={nowPnl.markPriceChange == 0 ? 'mark-quote-change zero' : isMarkUp ? 'mark-quote-change' : 'mark-quote-change down'}>
                <img src={nowPnl.markPriceChange == 0 ? NoVol : isMarkUp ? UpLogo : DownLogo} />
                {nowPnl.markPriceChange ? <DeriNumberFormat value={nowPnl.markPriceChange} decimalScale={2} suffix='%' /> : '0%'}
              </div>
            </div>
          </div>

        </div>
        <div className='chart'>
          <div className='max'>
            Profit
          </div>
          <div className='long-short'>
            <div className={isLong ? 'long check' : 'long'} onClick={() => { tabLong(true) }}>
              <img src={longLogo} alt='' />
              Long
            </div>
            <div className={isLong ? 'short' : 'short check'} onClick={() => { tabLong(false) }}>
              <img src={shortLogo} alt='' />
              Short
            </div>
          </div>
          <div className='chart-box'>
            {!data && <div className='loading-box'>
              <div className='loading'></div>
            </div>}
            <Chart arr={data} indexName={indexName} marketName={marketName} onChange={onChange} lastData={lastData} isLong={isLong} isFuture={trading.symbolInfo.isFuture} />
          </div>

          <div className='min'>
            Loss
          </div>
        </div>
      </div>
      <div className='simulate-pnl-footer'>
        <div className='market'>{lang['market']}</div>
        <div className='DVol-symbol'>
          <div className='symbol-name'>
            {indexName}
          </div>
          <div className='DVol-price'>
            {trading.symbolInfo.isFuture && <DeriNumberFormat value={trading.index} decimalScale={2} />}
            {!trading.symbolInfo.isFuture && <><DeriNumberFormat value={trading.index} decimalScale={2} /> | DVOL : <DeriNumberFormat value={trading.tradeInfo.volatility * 100} decimalScale={2} suffix='%' /></> }
          </div>
        </div>
        <div className='mark-name-price'>
          <div className='mark-name'>
            {marketName}
          </div>
          <div className='mark-price'>
            <DeriNumberFormat value={trading.markPrice} decimalScale={2} />
          </div>
        </div>
      </div>
    </div>
  )
}