import React, { useState,useEffect } from 'react'
import RcSlider,{SliderTooltip} from 'rc-slider'
import 'rc-slider/assets/index.css';
import {calculateMinDecimal } from '../../utils/utils';
export default function Slider({min,max,value,defaultValue,onSlide,className,trackStyle = {},railStyle = {},handleStyle = {}}){
  
  return (
    <RcSlider
        min={0}
        max={+max}
        value={+value}
        className={className}
        onChange={onSlide}
        defaultValue={defaultValue}
        trackStyle={{...trackStyle}}
        handleStyle={{...handleStyle}}
        railStyle={{...railStyle}}
        step={calculateMinDecimal(max)}
      />
  )
}