import React, { useState, useEffect } from 'react'
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';
import Tip from './Tip'
export default function Chart({ arr, indexName, marketName, onChange, lastData, isLong,isFuture }) {
  const [data, setData] = useState(arr)
  const [parentRect, setParentRect] = useState({})
  const [off, setOff] = useState(0);
  const [isDefault, setIsDefault] = useState(false)
  const getDefault = (bool) => {
    setIsDefault(bool)
  }
  const gradientOffset = () => {
    const dataMax = Math.max(...data.map((i) => i.markPriceChange));
    const dataMin = Math.min(...data.map((i) => i.markPriceChange));

    if (dataMax <= 0) {
      return 0;
    }
    if (dataMin >= 0) {
      return 1;
    }
    console.log(dataMax / (dataMax - dataMin))
    setOff(dataMax / (dataMax - dataMin))
  };

  useEffect(() => {
    const rect = document.querySelector('.chart-box').getBoundingClientRect()
    setParentRect(rect)
    setData(arr)
  }, [arr])
  useEffect(() => {
    if (data) {
      gradientOffset()
    }
  }, [data])
  return (data ?
    <ResponsiveContainer>
      <AreaChart width={parentRect.width - 50} data={data}>
        <CartesianGrid vertical={true} horizontal={true} horizontalPoints={[isFuture ? 100 : isLong ? 148 : 52]}
          stroke="#203B60" />
        <XAxis dataKey="indexPrice" axisLine={false} hide={true} tickLine={false} tick={{ fill: '#fff', fontSize: '14' }} allowDataOverflow={false} />
        <YAxis dataKey="markPriceChange" tickLine={false} axisLine={false} hide={true} tick={{ fill: '#fff', fontSize: '14' }} />
        <Tooltip position={isDefault ? { x: 200, y: 0 } : null} cursor={true} active={true} content={<Tip index={indexName} market={marketName} onChange={onChange} getDefault={getDefault} lastData={lastData} />} />
        <defs>
          <linearGradient id="splitColor" x1="0" y1="0" x2="0" y2="1">
            <stop offset={off} stopColor="#59AE99" stopOpacity={0.5} />
            <stop offset={off} stopColor="#CE5E5E" stopOpacity={0.5} />
          </linearGradient>
        </defs>
        <Area type="monotone" dataKey="markPriceChange" stroke="#3756CD" fillOpacity={0.5} strokeWidth={2} fill="url(#splitColor)" />
      </AreaChart>

    </ResponsiveContainer>
    : null)
}