import classNames from 'classnames';
import React, { useState } from 'react'
import ButtonGroup from '../../../../components/ButtonGroup/ButtonGroup';
import Checkbox from '../../../../components/Checkbox/Checkbox';
import Icon from "../../../../components/Icon/Icon";
import Input from '../../../../components/Input/Input';
import UnderlineText from '../../../../components/UnderlineText/UnderlineText';

export default function AdvancedSetting({lang,onSlippageChange,skipConfirmation,onSkipConfirmationChange,onToggleChange}){
  const [isOpen, setIsOpen] = useState(false)
  const [slippageValue, setSlippageValue] = useState('')
  const [defaultValue, setDefaultValue] = useState('0.5')
  const slippageClazz = classNames('slippage',{
    show : isOpen
  })
  const slippageValueClass = classNames('slippage-input',{
    selected : slippageValue !== ''
  })
  const onToggle = () => {
    const current = !isOpen;
    setIsOpen(current)
    onToggleChange && onToggleChange(current)
  }
  const onSelect = value => {
    if(/%$/.test(value)){
      const slippage  = value.replace(/%/,'')
      onSlippageChange(slippage);
      setSlippageValue('')
      setDefaultValue(slippage)
    }
  }
  const onChange = value => {
    setSlippageValue(value)
    onSlippageChange(value)
    if(/\d+$/.test(defaultValue)) {
      setDefaultValue(`${defaultValue}%`)
    }
  }
  const onBlur = event => {
    let value = event.target.value
    if(value === ''){
      // value = '0.1'
      setDefaultValue(defaultValue.replace('%',''))
      setSlippageValue('')
    } else {
      onSlippageChange(value)
    }
  }
  const onFocus = value => {
    if(/\d+$/.test(defaultValue)) {
      setDefaultValue(`${defaultValue}%`)
    }
  }
  const onCheckboxChanged = checked => {
    onSkipConfirmationChange(checked)
  }
  return(
    <div className='advanced-setting'>
      <div onClick={onToggle} className='toggle'>
        {lang['advanced-setting']} <Icon token={isOpen ? 'arrow-up' : 'arrow-down'} width='24'/>
      </div>
      <div className={slippageClazz}>
        <div className='title'><UnderlineText tip={lang['slippage-tolerance-hover']} >{lang['slippage-tolerance']}</UnderlineText></div>
        <div className='slippage-value'>
          <Input className={slippageValueClass} step={0.0001} minTradeVolume={0.0001} max={100} decimal={4} value={slippageValue} unit='%' onBlur={onBlur} onFocus={onFocus} onChange={onChange} />
          <ButtonGroup groups={['0.1%','0.2%','0.5%']} defaultValue={`${defaultValue}%`} className='slippage-group' onChange={onSelect} canUnselected={false} />
        </div>
        <div className='skip-confirm' >
          <Checkbox  checked = {skipConfirmation} onChange={onCheckboxChanged} className='l-checkbox'/>
          <span className='confirm-text' onClick={() => onCheckboxChanged(!skipConfirmation)}>{lang['skip-transaction-confirm']}</span>
        </div>
      </div>
    </div>
  )
}