import React, { useState, useEffect, useRef, useCallback } from 'react'
import closeModalIcon from '../../assets/img/close-modal-icon.svg'
import Input from '../Input/Input'
import './marginPanel.less'
import { hide } from 'react-functional-modal'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import ButtonGroup from '../ButtonGroup/ButtonGroup'
import classNames from 'classnames'
import ApiProxy from '../../model/ApiProxy'
import Icon from '../Icon/Icon'
import { DEPOSIT, WITHDRAW } from '../../utils/Constants'
import { percentTimesAmount, getDefaultNw, buried } from '../../utils/utils'
import TransactionButton from '../TransactionButton/TransactionButton';
import { bg, DeriEnv } from '../../lib/web3js'
import Search from '../Search/Search'
import RemoveHint from '../RemoveHint/RemoveHint'
import styled from 'styled-components'
import { useStateWithCallback } from '../../hooks/useStateWithCallback'

function MarginPanel({ trading, lang, type, wallet, pool, alert, getLang, close }) {
  const [selectedBToken, setSelectedBToken] = useState({})
  const [discount, setDiscount] = useState([])
  const [baseTokens, setBaseTokens] = useState([])
  const [amount, setAmount] = useState('')
  const [defaultPercent, setDefaultPercent] = useState('');
  const [marginChangedSummary, setMarginChangedSummary] = useState({})
  const [loaded, setLoaded] = useState(false)
  const [tradeInfo, setTradeInfo] = useState({})
  const [isMax, setIsMax] = useState(false)
  const [disableInput, setDisableInput] = useState(true)
  const [balance, setBalance] = useState('')
  const [onInputing, setOnInputing] = useState(false)

  const marginChangeDescClass = classNames('mp-desc', {
    show: amount > 0
  })

  const click = async () => {
    const method = type === DEPOSIT ? 'depositMargin' : 'withdrawMargin'
    let max = selectedBToken.availableBalance
    if (type === WITHDRAW) {
      if (/\d+\.(999999|099999|009999|000999|000099|000009)/.test(max)) {
        max = (+selectedBToken.availableBalance) * 1.01
      }
    }
    if (type === WITHDRAW && bg(amount).gt(max)) {
      alert.error(lang['under-margin']);
      return;
    }
    if ((+amount) <= 0 || isNaN(amount)) {
      alert.error(lang['it-has-to-be-greater-than-zero-tip']);
      return;
    }
    const params = pool.isAllV3
      ? [wallet.detail.chainId, pool.address, wallet.detail.account, amount, selectedBToken.bTokenSymbol]
      : [wallet.detail.chainId, pool.address, wallet.detail.account, amount, selectedBToken.bTokenId]
    if (isMax && type === WITHDRAW) {
      params.push(true)
    }

    const res = await ApiProxy.request(method, params, {
      includeResponse: true,
      write: true,
      subject: `${lang[type]} ${lang['margin']}`,
      title: {
        processing: `${type === DEPOSIT ? lang['deposit'] : lang['withdraw']} ${lang['margin']} ${lang['processing']}`,
        success: `${type === DEPOSIT ? lang['deposit'] : lang['withdraw']} ${lang['margin']} ${lang['executed']}`,
        error: `${type === DEPOSIT ? lang['deposit'] : lang['withdraw']} ${lang['margin']} ${lang['failed']}`,
      },
      content: {
        success: `${type === DEPOSIT ? lang['deposit'] : lang['withdraw']}  ${selectedBToken.bTokenSymbol} ${lang['margin']}`,
        error: lang['transaction-failed'],
      },
      onAccept: () => close()
    })
    if (res && res.success) {
      if (type === DEPOSIT) {
        buried("marig_deposit", "deposit", "complete_deposit")
      }
      trading.refresh()
    }
  }

  const selectBToken = (index, clear) => {
    if (selectedBToken.bTokenSymbol !== baseTokens[index].bTokenSymbol) {
      if (clear) {
        setAmount('')
        setDefaultPercent('')
      }
      const cur = baseTokens[index];
      setSelectedBToken(cur)
      const value = type === WITHDRAW ? cur.availableBalance : cur.walletBalance
      wrapBalance(value, baseTokens[index].bTokenSymbol)
    }
  }

  //usdc‘s decimal is six for arbitrum
  const wrapBalance = (value, bToken) => {
    if (wallet.isArbi && bToken === 'USDC' && value < 1 * 10 ** -6) {
      setBalance(0)
    } else {
      setBalance(value)
    }
  }

  const updateSelected = useCallback((bTokens) => {
    const newer = bTokens.find(token => token.bTokenSymbol === selectedBToken.bTokenSymbol)
    if (type === WITHDRAW && selectedBToken.availableBalance !== newer.availableBalance) {
      const value = selectedBToken.availableBalance = newer.availableBalance
      wrapBalance(value, selectedBToken.bTokenSymbol)
    }
    if (type === DEPOSIT && selectedBToken.walletBalance !== newer.walletBalance) {
      const value = selectedBToken.walletBalance = newer.walletBalance
      wrapBalance(value, selectedBToken.bTokenSymbol)
    }
  }, [selectedBToken, defaultPercent])
  //加载
  const loadBToken = async () => {
    if (pool && pool.selectedSymbol) {
      let params;
      if (wallet.isConnected()) {
        params = [wallet.detail.chainId, pool.selectedSymbol.address, wallet.detail.account]
      } else {
        params = [wallet.detail.chainId || getDefaultNw(DeriEnv.get()).id, pool.selectedSymbol.address, "0x0000000000000000000000000000000000000000"]
      }
      const bTokens = await ApiProxy.request('getUserBTokensInfo', params)
      setLoaded(true)
      if (bTokens.length > 0) {
        setBaseTokens(bTokens.sort((t1, t2) => {
          const bal1 = (+t1.availableBalance)
          const bal2 = (+t2.availableBalance)
          if (bal1 === 0 && bal2 > 0) {
            return 1;
          } else if (bal1 > 0 && bal2 === 0) {
            return -1;
          } else {
            return 0;
          }
        }))
        //update selectedBToken && update percent
        if (selectedBToken.bTokenSymbol) {
          updateSelected(bTokens)
        } else {
          const value = type === WITHDRAW ? bTokens[0].availableBalance : bTokens[0].walletBalance
          wrapBalance(value, bTokens[0].bTokenSymbol)
          setSelectedBToken(bTokens[0])
        }

      }
    }
  }

  const loadBTokenDiscount = async () => {
    if (wallet && pool && pool.isAllV3) {
      const discount = await ApiProxy.request('getBTokenDiscount', [wallet.detail.chainId, pool.address]);
      setDiscount(discount)
    }
  }

  const loadTradeInfo = async (selectedBToken) => {
    if (pool.isAllV3 && selectedBToken && selectedBToken.bTokenSymbol) {
      const tradeInfo = await ApiProxy.request('getEstimatedTdInfo', [wallet.detail.chainId, pool.selectedSymbol.address, wallet.detail.account, selectedBToken.bTokenSymbol])
      setTradeInfo(tradeInfo);
    }
  }

  const onChange = value => {
    setAmount(value)
    setDefaultPercent(value)
  }


  const onSelect = value => {
    if (value) {
      // const balance = type === DEPOSIT ? selectedBToken.walletBalance : selectedBToken.availableBalance
      const result = percentTimesAmount(value, balance)
      setDefaultPercent(value)
      setAmount(result)
      if (value === '100%') {
        setIsMax(true)
      } else {
        setIsMax(false)
      }
    }
  }

  useEffect(() => {
    loadBTokenDiscount();
  }, [wallet, pool])

  useEffect(() => {
    loadBToken()
  }, [trading.accountInfo, pool, wallet])

  useEffect(() => {
    loadTradeInfo(selectedBToken);
  }, [trading.accountInfo, selectedBToken.bTokenSymbol])

  useEffect(async () => {
    if (trading.accountInfo && tradeInfo) {
      const { dynEffMargin, marginUsage, availableMargin } = trading.accountInfo
      const changed = {}
      const bTokenDiscount = discount.find(d => d.bTokenSymbol === selectedBToken.bTokenSymbol) || {}
      const value = bg(type === WITHDRAW ? -amount : amount).times(selectedBToken.bTokenPrice || 1).times(bTokenDiscount.discount || 1).toNumber()
      changed.dynEffMargin = +bg(dynEffMargin).plus(value).toString() <= 0 ? 0 : bg(dynEffMargin).plus(value).toString();
      changed.availableMargin = +bg(availableMargin).plus(value).toString() <= 0 ? 0 : bg(availableMargin).plus(value).toString();
      changed.marginUsagePercent = 0;
      if (changed.dynEffMargin) {
        const value = (marginUsage / changed.dynEffMargin).toFixed(2)
        if (value >= 1) {
          changed.marginUsagePercent = '100'
        } else {
          changed.marginUsagePercent = value * 100
        }
      }
      if ((+tradeInfo.b0Penalty) < 0 && Math.abs(value) <= (-tradeInfo.b0Penalty) && type === WITHDRAW) {
        changed.dynEffMargin = dynEffMargin
        changed.availableMargin = availableMargin
      } else if ((+tradeInfo.b0Penalty) < 0 && type === WITHDRAW) {
        changed.dynEffMargin = +bg(dynEffMargin).plus(value).minus(tradeInfo.b0Penalty).toString() <= 0 ? 0 : bg(dynEffMargin).plus(value).minus(tradeInfo.b0Penalty).toString();
        changed.availableMargin = +bg(availableMargin).plus(value).minus(tradeInfo.b0Penalty).toString() <= 0 ? 0 : bg(availableMargin).plus(value).minus(tradeInfo.b0Penalty).toString();
      }
      if (type === WITHDRAW) {
        let res = await ApiProxy.request('getEstimatedTdInfo', [wallet.detail.chainId, pool.selectedSymbol.address, wallet.detail.account, selectedBToken.bTokenSymbol, amount])
        if (res) {
          changed.actualWithdrawAmount = res.actualWithdrawAmount
        }
      }
      setMarginChangedSummary(changed)
    }
  }, [trading.accountInfo, tradeInfo, amount])

  useEffect(() => {
    if (amount && +amount !== 0) {
      let newAmount = amount
      if (amount && defaultPercent && balance && !onInputing && defaultPercent === "100%") {
        newAmount = percentTimesAmount(defaultPercent, balance)
        setAmount(newAmount)
      }
      if (type === WITHDRAW) {
        let max = balance
        if (/\d+\.(999999|099999|009999|000999|000099|000009)/.test(max)) {
          max = (+balance) * 1.01
        }
        if (bg(newAmount).gt(max)) {
          setDisableInput(true)
        } else {
          setDisableInput(false)
        }
      } else {
        if (bg(newAmount).gt(balance)) {
          setDisableInput(true)
        } else {
          setDisableInput(false)
        }
      }
    } else {
      setDisableInput(true)
    }
  }, [amount, balance, defaultPercent])

  return (
    <div className='margin-panel'>
      <div className='mp-title'>
        <div>{lang['account']}&nbsp;{lang[type.toLowerCase()]}</div>
        <img src={closeModalIcon} alt='' onClick={close} />
      </div>
      <div className='mp-body'>
        <div className='b-token-list'>
          <Search list={baseTokens} width='380' height='48' className='search' onChange={list => setBaseTokens(list)} />
          <div className='mp-header'>
            <div className='mp-col'>{lang['base-token']}</div>
            <div className='mp-col'>{lang['wallet-balance']}</div>
            <div className='mp-col'>{lang['available-balance']}</div>
            {pool.isAllV3 && !pool.singleBtoken && <div className='mp-col'>{lang['disc-factor']}</div>}
          </div>
          <div className='mp-list'>
            <ListBody loaded={loaded} baseTokens={baseTokens} pool={pool} selectedBToken={selectedBToken} selectBToken={index => selectBToken(index, true)} lang={lang} />
          </div>
        </div>
        <div className='mp-main'>
          <div className='mp-input'>
            <Input value={amount} inputWidth={'70%'} decimal='18' onChange={value => onChange(value)} onFocus={() => setOnInputing(true)} onBlur={() => setOnInputing(false)} unit={selectedBToken.bTokenSymbol} />
          </div>
          <div className='main-title'>
            <BalanceDisplay balance={balance} type={type} lang={lang} bTokenSymbol={selectedBToken.bTokenSymbol} />
            {
              type === WITHDRAW
                ?
                <RemoveHint getLang={getLang} wallet={wallet} lang={lang} baseToken={selectedBToken.bTokenSymbol} isB0Staked={tradeInfo.isB0Staked} penalty={tradeInfo.b0Penalty} availableBalance={selectedBToken.availableBalance} />
                :
                selectedBToken.bTokenSymbol === 'BUSD' && <RemoveHint getLang={getLang} wallet={wallet} lang={lang} baseToken={selectedBToken.bTokenSymbol} isB0Staked={tradeInfo.isB0Staked} penalty={tradeInfo.b0Penalty} availableBalance={1} />
            }
          </div>
          <div className='quick-help'>
            <ButtonGroup groups={['25%', '50%', '75%', '100%']} selectedBorderColor='#CD7A37' defaultValue={defaultPercent} onChange={value => onSelect(value)} outline={true} bgColor='rgba(85, 119, 253, 0.1)' width={77} height={40} />
          </div>
          <div className={marginChangeDescClass}>
            <div className='mp-d-row'>
              <div>{lang['margin-usage']}:</div>
              <div>
                <DeriNumberFormat value={trading.accountInfo.marginUsagePercent} suffix='%' decimalScale={2} />&nbsp;{"->"}&nbsp;
                <DeriNumberFormat value={marginChangedSummary.marginUsagePercent} suffix='%' allowZero decimalScale={2} /></div>
            </div>
            <div className='mp-d-row'>
              <div>{lang['available-margin']}:</div>
              <div><DeriNumberFormat value={trading.accountInfo.availableMargin} decimalScale={2} /> &nbsp;{"->"}&nbsp;
                <DeriNumberFormat value={marginChangedSummary.availableMargin} decimalScale={2} allowZero /></div>
            </div>
            <div className='mp-d-row'><div>
              {lang['dynamic-effective-balance']}:</div>
              <div>
                <DeriNumberFormat value={trading.accountInfo.dynEffMargin} decimalScale='2' />&nbsp;{"->"}&nbsp;
                <DeriNumberFormat value={marginChangedSummary.dynEffMargin} decimalScale='2' allowZero />
              </div>
            </div>
            {type === WITHDRAW && <div className='mp-d-row'><div>
              {lang['est-amount-received']}:</div>
              <div>
                <DeriNumberFormat value={marginChangedSummary.actualWithdrawAmount} decimalScale='2' />
              </div>
            </div>}
          </div>
          <div className='mp-footer'>
            <TransactionButton isBuried={type === DEPOSIT} lang={lang} disabled={disableInput} key={selectedBToken.bTokenSymbol} pool={pool} bTokenId={selectedBToken.bTokenId} alert={alert} bToken={selectedBToken.bTokenSymbol} trading={trading} wallet={wallet} icon={type} label={lang[type.toLowerCase()].toUpperCase()} fontSize={18} fontWeight={700} width={332} height={56} onClick={click} />
          </div>
        </div>
      </div>
    </div>
  )
}


function ListBody({ loaded, baseTokens, selectBToken, selectedBToken, pool, lang }) {
  return (
    loaded
      ?
      baseTokens.map((bToken, index) => {
        const clazz = classNames('mp-row', { selected: bToken.bTokenSymbol === selectedBToken.bTokenSymbol })
        let apy = bg(bToken.supplyApy || 0).plus(bToken.xvsApy || 0).toFixed(3)
        apy = (+apy) === 0 ? '0' : apy
        return (
          <div key={index} className={clazz} onClick={() => selectBToken(index)}>
            <div className='mp-col align-left'>
              <Icon token={bToken.bTokenSymbol.toUpperCase()} width={20} height={20} />
              <span >
                <div>{bToken.bTokenSymbol.toUpperCase()}</div>
                {pool.isAllV3 && !pool.singleBtoken && <div className='apy'>{lang['apy']}&nbsp;&nbsp;<DeriNumberFormat suffix='%' value={apy} decimalScale='2' allowZero width='12px' /></div>}
              </span>
            </div>
            <div className='mp-col'><DeriNumberFormat value={bToken.walletBalance} allowZero={true} decimalScale={2} /></div>
            <div className='mp-col'><DeriNumberFormat value={bToken.availableBalance} allowZero={true} decimalScale={2} /></div>
            {(pool.isV3 || pool.isV3Lite) && !pool.singleBtoken && <div className='mp-col'>{bToken.discount}</div>}
          </div>
        )
      })
      :
      <div className='mp-row loading-line'></div>
  )
}

const BalanceDisplayWrapper = styled.div`
  display : flex;
  item-aligns : center;
`

function BalanceDisplay({ lang, type, balance, bTokenSymbol }) {
  const text = type === DEPOSIT ? lang['wallet-balance'] : lang['available-balance']
  return (
    <BalanceDisplayWrapper>
      {text}: <DeriNumberFormat value={balance} suffix={` ${bTokenSymbol}`} decimalScale={2} />
    </BalanceDisplayWrapper>
  )
}

export default MarginPanel
