import classNames from 'classnames';
import React,{useState,useEffect,useImperativeHandle} from 'react';
import { forwardRef } from 'react';
import { useRef } from 'react';
import Icon from '../../../../components/Icon/Icon';
import {isScrollRight } from '../../../../utils/utils';
import './fixedColumn.less'

export default forwardRef((props,ref) => {
  const [fixedColumn, setFixedColumn] = useState(false)
  const [isRightmost, setIsRightmost] = useState(false)
  const [isLeftmost, setIsLeftmost] = useState(true)
  const bodyRef = useRef()

  useImperativeHandle(ref,() => {
    const body  =bodyRef.current
    if(body){
      body.onscroll = event => {
        props.onScroll(event)
      }
    }
    return body
  })

  const scrollRight = (e) => {
    e && e.stopPropagation();
    const posBody = document.querySelector(props.selector)
    if(isScrollRight(posBody)){
      return 
    }
    let scrollAmount = 0;
    const slideTimer = window.setInterval(() => {
      posBody.scrollLeft += 10
      scrollAmount = scrollAmount + 10
      if(scrollAmount >= 100) {
        window.clearInterval(slideTimer);
        if(isScrollRight(posBody)) {
          setIsRightmost(true)
          setIsLeftmost(false)
        } else {
          setIsRightmost(false)
          setIsLeftmost(true)
        }
      } 
    },20)
  }

  const scrollLeft = (e) => {
    e && e.stopPropagation();
    const posBody = document.querySelector(props.selector)
    if(posBody.scrollLeft <= 0){
      return ;
    }
    let scrollAmount = 0;
    const slideTimer = window.setInterval(() => {
      posBody.scrollLeft -=10
      scrollAmount = scrollAmount + 10
      if(scrollAmount >= 100 || posBody.scrollLeft <= 0) {
        window.clearInterval(slideTimer);
      }
      if(posBody.scrollLeft <= 0){
        setIsLeftmost(true)
        setIsRightmost(false)
      } else {
        setIsLeftmost(false)
        setIsRightmost(true)
      }
    },20)
  }

  useEffect(() => {
    const {selector} = props
    const obj = document.querySelector(selector)
    const resizeHandler = () => {
      const mainBody = document.querySelector(props.selector)
      // mainBody.scrollLeft = 0;
      if(obj.scrollWidth > obj.clientWidth) {
        setFixedColumn(true)
      } else {
        setFixedColumn(false)
      }
    }
    resizeHandler()
    window.addEventListener('resize',resizeHandler)
    return () => window.removeEventListener('resize',resizeHandler)
  }, [props.data]);



  return props.data.length > 0 && fixedColumn ? <div className='fixed-column'>
      <div className='arrow'>
        <Icon token={isRightmost ? 'p-arrow-right-disabled' : 'p-arrow-right'} width='24' onClick={scrollRight}/> 
        <Icon token={isLeftmost ? 'p-arrow-left-disabled' : 'p-arrow-left'} width='24' onClick={scrollLeft}/>
      </div>    
      <div className='container'>
        <div className='f-header'>
          <div className='arrow-left'></div>
          <div className='f-row'>
            <div className='f-col'>{props.headerLabel}</div>
          </div>
        </div>
        <div className='f-body' ref={bodyRef}>
          <div className='columns'>
            {props.data.map((p,i) => (
              <div className='f-row' key={i}>
                <div className='f-col' >{props.renderFixedElement && props.renderFixedElement(p)}</div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div> 
    : null
})

