import React, { useMemo } from 'react'
import styled from 'styled-components';
import Tab from '../../../components/Tab/Tab';
import LeverageBar from './LeverageBar';
import { BUY, SELL } from '../../../utils/Constants';

const TabWrapper = styled(Tab)`
  &.leveragebar-tab {
    min-height : 696px;
  }
  @media only screen and (min-width : 375px) {
    &.leveragebar-tab {
      min-height : inherit;
    }
  }
  @media only screen and (min-width : 760px) {
    &.leveragebar-tab {
      min-height : 721px;
      max-height : 742px;
    }
  }
`

export default function Orderbar({lang}){
  const tabSet = useMemo(() => (
    {[lang['buy']] : {
      content: <LeverageBar direction={BUY} lang={lang}/>,
      style: { 
        background: 'linear-gradient(180deg, rgba(172, 225, 255, 0.08) 0.16%, rgba(66, 143, 124, 0.08) 70.66%)'
      },
      borderColor :"rgba(89, 174, 153, 0.3)",
      direction: "tab-left",
      fontColor : "#59AE99"
    },
    [lang['sell']] : {
      content : <LeverageBar direction={SELL}  lang={lang}/>,
      style : {
        background: 'linear-gradient(180deg, rgba(206, 94, 94, 0.08)-0.02%, rgba(206, 94, 94, 0.08) 80.65%)'
      },
      borderColor:"rgba(206, 94, 94, 0.2)",
      direction:"tab-right",
      fontColor : "#CE5E5E"
    }
  }),[lang])
  return(
    <div>
      <div className='retired-trade-mask'></div>
      <TabWrapper tabSet={tabSet} width='100%' className='leveragebar-tab' height={44} size={18} inactiveBgColor='#0A172B' borderTop={true}  borderBottom={true} divider={true}/>
    </div>
  )
}