import DeriNumberFormat from "../../utils/DeriNumberFormat"
import DownLogo from '../../assets/img/pnlDown.svg'
import UpLogo from '../../assets/img/pnlUp.svg'
const Tip = ({ index, market, active, payload, onChange,lastData,getDefault}) => {
  if (active && payload && payload.length) {
    onChange(payload)
    getDefault(false)
    return (<div className='tip-chart'>
      <div className='tip-index-price-pnl'>
        <div className='tip-index-name'>
          {index}
        </div>
        <div className='tip-index-price'>
          <DeriNumberFormat value={payload[0].payload.indexPrice} decimalScale={2} />
        </div>
        <div className={payload[0].payload.indexPriceChange >= 0 ? 'tip-index-pnl' : 'tip-index-pnl down'}>
          <img src={payload[0].payload.indexPriceChange >= 0 ? UpLogo : DownLogo} />
          <DeriNumberFormat value={payload[0].payload.indexPriceChange} decimalScale={2} suffix='%' />
        </div>
      </div>
      <div className='tip-market-price-pnl'>
        <div className='tip-market-name'>{market}</div>
        <div className='tip-market-price'>
          <DeriNumberFormat value={payload[0].payload.markPrice} decimalScale={2} />
        </div>
        <div className={payload[0].payload.markPriceChange >= 0 ? 'tip-market-pnl' : 'tip-market-pnl down'}>
          <img src={payload[0].payload.markPriceChange >= 0 ? UpLogo : DownLogo} />
          <DeriNumberFormat value={payload[0].payload.markPriceChange} decimalScale={2} suffix='%' />
        </div>
      </div>

    </div>)
  } else if(lastData.indexPrice){
    let arr = []
    let payload = {
      payload :lastData
    }
    arr.push(payload)
    onChange(arr)
    getDefault(true)
    return null
  }else{
    let arr = []
    let payload = {
      payload :lastData
    }
    arr.push(payload)
    onChange(arr)
    return null
  }
}

export default Tip