import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import gaugeGraph from '../../../assets/img/gauge-graph.svg'
import DeriNumberFormat from '../../../utils/DeriNumberFormat'
import Button from '../../../components/Button/Button'
import TransactionButton from '../../../components/TransactionButton/TransactionButton'
import { SECONDARY, DEPOSIT, WITHDRAW } from '../../../utils/Constants'
import UnderlineText from '../../../components/UnderlineText/UnderlineText'
import { show, hide } from 'react-functional-modal'
import MarginPanel from '../../../components/MarginPanel/MarginPanel'
import { observer, inject } from 'mobx-react';
import './accountInfo.less'
import { useAlert } from 'react-alert'
import Gauge from '../../../components/Gauge/Gauge';
import TradeWarnning from '../Sidebar/TradeWarnning'
import classNames from 'classnames'
import Icon from '../../../components/Icon/Icon'
import ApiProxy from '../../../model/ApiProxy'
import Type from '../../../model/Type'
import { useModal } from 'react-hooks-use-modal';
import { buried } from '../../../utils/utils'


function DynEffectiveBalanceDisplay({ wallet, trading, lang, dynEffMargin }) {
  const [isShowCollect, setIsShowCollect] = useState(false)
  const alert = useAlert()
  const showClass = classNames('xvs-earned', {
    show: isShowCollect
  })
  const closeCollect = e => {
    e.stopPropagation()
    setIsShowCollect(!isShowCollect)
  }
  const claimVenusReward = async (e) => {
    e.stopPropagation()
    const params = [wallet.detail.chainId, trading.symbolInfo.address, wallet.detail.account]
    const res = await ApiProxy.request('claimXVSTd', params, {
      includeResponse: true, write: true, subject: lang['collect'],
      title: {
        processing: lang['claim-XVS-processing'],
        success: lang['claim-XVS-reward'],
        error: lang['claim-reward-failed'],
      },
      content: {
        success: lang['claim-XVS-reward'],
        error: lang['transaction-failed'],
      }
    })
    closeCollect(e)
  }
  useEffect(() => {
    const close = () => setIsShowCollect(false)
    document.addEventListener('click', close, false)
    return () => {
      document.removeEventListener('click', close)
    }
  }, [trading.venusEarned])
  return trading.symbolInfo.isAllV3 && !trading.symbolInfo.singleBtoken && wallet.supportClaimXVS
    ? <div className='value v3'>
      <div className='dyn-eff-bal'>
        <DeriNumberFormat value={dynEffMargin} decimalScale='2' suffix={` ${trading.tradeBToken}`} closeShowLoadingEffectAfterLoaded={true} />
        <div><Icon token={isShowCollect ? 'arrow-up' : 'arrow-down'} alt='collect' width='24' height='24' onClick={closeCollect} /></div>
      </div>
      <div className={showClass}>
        <span className='label'>{lang['xvs-earned']} </span>
        <DeriNumberFormat decimalScale='2' value={trading.venusEarned} width='16px' className='claimed' closeShowLoadingEffectAfterLoaded={true} />
        <TransactionButton subject={`XVS ${lang['collect']}`} bgColor='rgba(55, 86, 205, 0.5)' disabled={(+trading.venusEarned) <= 0} ignoreApprove={true} className='collect' onClick={claimVenusReward} fontSize='14' fontWeight='400' width='70' height='24' label={lang['collect']} />
        {/* <Button bgColor='rgba(55, 86, 205, 0.5)' disabled={(+trading.venusEarned) === 0} className='collect' onClick={claimVenusReward} fontSize='14' fontWeight='400' width='70' height='24' label={lang['collect']}/> */}
      </div>
    </div>
    : <div className='value'><DeriNumberFormat value={dynEffMargin} decimalScale='2' suffix={` ${trading.tradeBToken}`} closeShowLoadingEffectAfterLoaded={true} /> </div>
}

function AccountInfo({ trading, wallet, lang, getLang }) {
  const alert = useAlert()
  const [action, setAction] = useState(DEPOSIT)
  const [MarginPanelModal, openMarginPanelModal, closeMarginPanelModal] = useModal('root', {
    preventScroll: false,
    closeOnOverlayClick: false
  });
  const [isShowWarn, setIsShowWarn] = useState(false)
  const [warnContent, setWarnContent] = useState('')
  const showWarnClass = classNames({ show: isShowWarn })
  const showMarginPanel = (type) => {
    if (type === DEPOSIT) {
      buried("open_deposit", "deposit", "strat_deposit")
    }
    setAction(type)
    openMarginPanelModal()
  }
  useEffect(() => {
    if (trading.accountInfo) {
      if (trading.accountInfo.marginUsagePercent >= 100) {
        setWarnContent(lang['over-margin-100%-warn-hover'])
        setIsShowWarn(true)
      }
      if (trading.accountInfo.marginUsagePercent >= 180) {
        const tip = Type.isFuture ? lang['over-margin-180%-warn-hover'] : lang['over-margin-180%-option-warn-hover']
        setWarnContent(tip)
        setIsShowWarn(true)
      }
      if (trading.accountInfo.marginUsagePercent < 100) {
        setIsShowWarn(false)
      }
    }
  }, [trading.accountInfo, trading.symbolInfo.symbol, trading.venusEarned])

  useEffect(() => {
    let interval = null
    if (trading.symbolInfo.supportVenus) {
      interval = window.setInterval(() => trading.getVenusEarned(wallet, trading.symbolInfo), 180000)
    }
    return () => interval && clearInterval(interval)
  }, [trading.symbolInfo])

  return (
    <div className='account' >
      <div className='a-info'>
        <div className='content'>
          <div className='detail-graph'>
            <div className='detail'>
              <div className='title row'>{lang['account-info']}</div>
              <div className='row'>
                <div><UnderlineText text={lang['dynamic-effective-balance']} multiline tip={lang['dynamic-effective-balance-hover']} /></div>
                <DynEffectiveBalanceDisplay wallet={wallet} trading={trading} lang={lang} dynEffMargin={trading.accountInfo.dynEffMargin} />
              </div>
              <div className='row'>
                <div><UnderlineText text={lang['margin-usage']} tip={lang['margin-usage-hover']} /></div>
                <div className='value'><DeriNumberFormat key={trading.symbolInfo.symbol} value={trading.accountInfo.marginUsage} suffix={` ${trading.tradeBToken}`} closeShowLoadingEffectAfterLoaded={true} decimalScale='2' /></div>
              </div>
              <div className='row'>
                <div><UnderlineText text={lang['available-margin']} tip={lang['available-margin-hover']} multiline /></div>
                <div className='value'><DeriNumberFormat key={trading.symbolInfo.symbol} value={trading.accountInfo.availableMargin} decimalScale='2' closeShowLoadingEffectAfterLoaded={true} suffix={` ${trading.tradeBToken}`} /></div>
              </div>
            </div>
            <div className='graph'>
              <Gauge accountInfo={trading.accountInfo} lang={lang} />
            </div>
          </div>
          <TradeWarnning content={warnContent} className={showWarnClass} />
          <div className='info-footer'>
            <Button className='deposit-btn' lang={lang} trading={trading} type={SECONDARY} icon='deposit' isReady={trading.isAccountReady} outline label={lang['deposit'].toUpperCase()} onClick={() => showMarginPanel(DEPOSIT)} />
            <Button lang={lang} trading={trading} outline type={SECONDARY} icon='withdraw' isReady={trading.isAccountReady} label={lang['withdraw'].toUpperCase()} onClick={() => showMarginPanel(WITHDRAW)} />
          </div>
        </div>
      </div>
      <MarginPanelModal>
        <MarginPanel type={action} getLang={getLang} trading={trading} wallet={wallet} lang={lang} pool={trading.pool} alert={alert} close={closeMarginPanelModal} />
      </MarginPanelModal>
    </div>
  )
}
export default inject('wallet', 'trading')(observer(AccountInfo))