import React, { useEffect, useState, useCallback } from 'react';
import { inject, observer } from 'mobx-react'
import useConfig from '../../hooks/useConfig'
import { DeriEnv } from '../../lib/web3js'
import { useWallet } from 'use-wallet'
import { useLocation } from 'react-router'
import { useModal } from 'react-hooks-use-modal';
import useLang from '../../hooks/useLang'
import walletImg from "./img/wallet.svg"
import switchImg from "./img/switch.svg"
import WalletDialog from '../WalletModal/WalletModal';
import pnc from '../../page-network-config.json'
import Icon from '../../components/Icon/Icon'
import { NETWORK_MAP } from '../../utils/Constants'
import { buried, equalIgnoreCase, getNetworkList } from '../../utils/utils'
import './TradeMaskHint.less'
export interface Props {
  wallet: any;
  intl: any;
  postion:String
}

function TradeMaskHint({ wallet, intl,postion }: Props) {
  const config = useConfig();
  const walletContext = useWallet()
  const lang = useLang(intl.dict, 'header');
  const location = useLocation();
  const [networkList, setNetworkList] = useState([])
  const [isShow, setIsShow] = useState(false);
  const [isConncet, setIsConnect] = useState(false)
  const [WalletModal, openWalletModal, closeWalletModal] = useModal('root', {
    preventScroll: true,
    closeOnOverlayClick: false
  });
  const changeNetWork = (value) => {
    const chainId = findChainIdByNetworkName(value)
    wallet.switchNetwork(config[chainId])
  }
  const findChainIdByNetworkName = (name: string) => {
    const chainIds = Object.keys(config);
    const pos = chainIds.findIndex(chainId => equalIgnoreCase(config[chainId].name, name))
    return chainIds[pos];
  }

  const getCurPageSupportedChains = useCallback(() => {
    const chainList = pnc[DeriEnv.get()];
    const pathname = location.pathname;
    const page = Object.keys(chainList).find(key => pathname.match(new RegExp(`^${key}`)))
    const supportedChains = chainList[page]
    return supportedChains
  }, [location])

  const showWalletModal = useCallback(() => {
    let value = "connection_2"
    if(postion !== "accountInfo" ){
      value = "connection_3"
    } 
    buried("wallet_connect","connect_locate",value)
    openWalletModal();
  }, [walletContext, lang])

  const isWrongNetwork = useCallback(() => {
    const supportedChains = getCurPageSupportedChains();
    if (walletContext.isConnected()) {
      setIsConnect(true)
      return supportedChains && !supportedChains.includes(walletContext.chainId)
    } else if (walletContext.status === "disconnected" || walletContext.status === 'error') {
      setIsConnect(false)
      return true
    }
    if (walletContext.status === 'connecting') {
      return false
    }
    return false
  }, [walletContext])

  useEffect(() => {
    if (isWrongNetwork()) {
      setIsShow(true)
    } else {
      setIsShow(false)
    }
  }, [walletContext]);

  useEffect(() => {
    const networkList = getNetworkList(DeriEnv.get(), getCurPageSupportedChains())
    setNetworkList(networkList)
  }, [location])

  return (
    isShow ? <div className="trade-mask">
      <div className="trade-mask-info">
        <div className="icon-trade-mask">
          <img src={isConncet ? switchImg : walletImg} alt="" />
        </div>
        <div className="text-trade-mask">
          {isConncet ? "Switch Your Networks" : "Please connect your wallet"}
          {isConncet && <div className="network-name-box">
            Please switch to
          {networkList.map(chain => (
            <span key={chain.name} className="network-name" onClick={() => { changeNetWork(chain.name) }}> {NETWORK_MAP[chain.name] || chain.name} </span>
          ))}
          </div>}
        </div>

        <div className="btn-trade-mask" onClick={isConncet ? () => { changeNetWork("Arbitrum") } : showWalletModal}>
          {isConncet ? "SWITCH" : "CONNECT"}
        </div>
      </div>
      <WalletModal>
        <WalletDialog walletContext={walletContext} closeModal={closeWalletModal} lang={lang} wallet={wallet} isWrongNetwork={isWrongNetwork} />
      </WalletModal>
    </div> : null
  )
}
export default inject('wallet', 'intl')(observer(TradeMaskHint))
