
import React, { useState, useEffect } from 'react'
import hint from './img/hint.svg'
import skip from './img/skip.svg'
import axios from 'axios'
import closeModalIcon from '../../assets/img/close-modal-icon.svg'
import { setCookie, getCookie, eqInNumber } from '../../utils/utils'
import apiProxy from '../../model/ApiProxy'
import './hint.less'
import { DeriEnv } from '../../lib/web3js/shared/config/env';
import { useWallet } from 'use-wallet'
function Announcement() {
  const [show, setShow] = useState(false)
  const [isSkip, setIsSkip] = useState(true)
  const [margin, setMargin] = useState(0)
  const [isVip, setIsVip] = useState(false)
  const wallet = useWallet()
  const closeHint = () => {
    if (isSkip) {
      setCookie('close-deri-hint', "false", 1)
    }
    setShow(false)
  }
  const getRetiredPoolsInfo = async () => {
    let res = await apiProxy.request('getRetiredPoolsInfo', ['56',wallet.account])
    if (res.length) {
      setMargin(res[0].dynamicEffectiveBalance)
    }

  }

  useEffect(() => {
    if (+margin > 0) {
      let cookie = getCookie('close-deri-hint')
      if (cookie === 'false') {
        setShow(false)
      } else {
        setShow(true)
      }
    }

  }, [margin])

  useEffect(() => {
    if (wallet.isConnected() && eqInNumber(wallet.chainId,56)) {
      getRetiredPoolsInfo()
    }
  }, [wallet.status])
  return (
    DeriEnv.get() === 'prod' ? <div>
      {show && <div className={isVip ? 'trade-hint  close-position-hint have-vip' : 'trade-hint  close-position-hint'}>
        <div className='hint-img'>
          <img src={hint} alt='' />
        </div>
        <div className='hint-box'>
          <div className='hint-title'>
            <span>
              For Your Information
            </span>
            <img className='close' src={closeModalIcon} alt='X' onClick={closeHint} />
          </div>
          <div className='hint-content'>
            <div className='hint-text vip-text'>
              You have a dynamic effective margin of {margin ? (+margin).toFixed(2) : 0} DERI in the <a target="_blank" href="https://deri.io/#/retired/trade/futures">retired market</a>.
              {/* You have a dynamic effective margin of {margin ? (+margin).toFixed(2) : 0} BUSD in the <a target="_blank" href="https://deri.io/#/retired/trade/futures">retired market</a>. */}
              <br />
              <br />
              Please be suggested to close positions/remove margin in the retired market, and reopen the position in this page after.
            </div>
            <div className='hint-link vip-skip'>
              <div className='learn-more'>
                <a target="_blank" rel="noreferrer" href='https://deri-protocol.medium.com/a-step-by-step-guide-to-close-positions-in-retired-markets-594ca6126adc'>Learn More</a>
              </div>
              <div className={isSkip ? 'skip check-skip' : 'skip'} onClick={() => setIsSkip(!isSkip)}>
                {isSkip && <img src={skip} />}
              </div>  <span>skip notification until tomorrow</span>
            </div>
          </div>
        </div>
        <div className='m-check' onClick={closeHint}>
          <span className='check'>
            OK
          </span>
        </div>
      </div>}

    </div> : null

  )
}
export default React.memo(Announcement)