import { useEffect, useState } from 'react'
import { hide } from 'react-functional-modal'
import hint from '../Announcement/img/hint.svg'
import './riskWarning.less'
export default function RiskWarning({back}) {
  const closeRisk = () => {
    hide("RiskWarning")
    back(true)
  }
  return (
    <div className="risk-warning">
      <div className="risk-header">
        <div className='risk-img'>
          <img src={hint} alt='' />
          Risks related to Derivatives Trading
        </div>
      </div>
      <div className="risk-box">
        Derivatives trading is a highly risky endeavor, with the potential for both great profits and significant losses. Please be aware that in the event of extreme price movement, there is a chance that all margin balance in your account may be liquidated.
      </div>
      <div className="risk-close">
        <div className="risk-close-btn" onClick={closeRisk}>
          OK, I Understand
        </div>
      </div>
    </div>
  )
}