import React, { useState, useEffect, useRef, useCallback } from 'react'
import { inject, observer } from "mobx-react"
import { isBrowser } from 'react-device-detect'
import DeriNumberFormat from "../../../../utils/DeriNumberFormat";
import { formatAddress, firstLetterUppercase, compareTwo } from "../../../../utils/utils";
import { useLocation, useHistory } from 'react-router-dom'
import classNames from "classnames";
import './position.less'
import ApiProxy from "../../../../model/ApiProxy";
import { useAlert } from 'react-alert'
import UnderlineText from "../../../../components/UnderlineText/UnderlineText";
import TradeMaskHint from "../../../../components/TradeMaskHint/TradeMaskHint"
import Type from "../../../../model/Type";
import { BUY } from "../../../../utils/Constants";
import styled from "styled-components";
import Button from '../../../../components/Button/Button';
import Icon from '../../../../components/Icon/Icon';
import html2canvas from 'html2canvas'
import { show, hide } from 'react-functional-modal'
import axios from 'axios'
import ShareModal from '../../../../components/ShareModal/ShareModal';
import Loading from '../../../../components/Loading/Loading';
import FixedColumn from '../FixedColumn/FixedColumn';
import { useStateWithCallback } from '../../../../hooks/useStateWithCallback'
import GridHeader from '../GirdHeader/GridHeader';
/**
 * price1 0--到markprice区间价格
 * price2 markprice到 无穷大的区间价格
 * futrues和power price1和price2只会有一个值
 * numPositions 当前options 个数，如btc-30000-c eth-2000-c = 2，futrues、power  = 0
 * price1=null price2 =null numPostions = 0 非options 无爆仓价格 
 * @param {*} param0 
 */
function LiqText({ price1, price2, underlier, lang, numPositions }) {
  return (
    <span className='liq-item'>
      <span>{underlier}: </span>
      <span>
        <span>
          {price1
            ? <DeriNumberFormat decimalScale={2} value={price1} />
            : numPositions > 1 ?
              <UnderlineText tip={lang['liq-price-hover-three']} text='?' />
              : numPositions !== 0 && <UnderlineText tip={lang['liq-price-hover-two']} text='--' />}
        </span>
        {numPositions > 0 && <span> / </span>}
        {price2
          ? <DeriNumberFormat decimalScale={2} value={price2} />
          : numPositions > 1 ?
            <UnderlineText tip={lang['liq-price-hover-three']} text='?' />
            : numPositions !== 0 && <UnderlineText tip={lang['liq-price-hover-two']} text='--' /> //can not be liquidation
        }
        {!price1 && !price2 && numPositions === 0 && <UnderlineText tip={lang['liq-price-hover-two']} text='--' />}
      </span>
      &nbsp;
    </span>
  )
}

const LiqPriceDisplay = ({ pos, lang }) => {
  const tatal = []
  pos.reduce((total, p) => {
    if (total.findIndex(item => item.underlier === p.liquidationPrice.underlier) === -1) {
      total.push(p.liquidationPrice)
    }
    return total;
  }, tatal)
  return (
    tatal.map((detail, index) => <LiqText numPositions={detail.numPositions} price1={detail.price1} price2={detail.price2} underlier={detail.underlier} key={index} lang={lang} />)
  )
}

const formatNumber = (number, decimal, suffix = '', prefix = '', allowZero) => {
  let num = Math.abs(number)
  if (/^[1-9]{1,}0{9,}$/.test(num)) {
    number = number.toString()
    number = number.replace(/000000000$/, '')
    suffix = ` g${suffix}`
  } else if (/000$/.test(num)) {
    number = number.toString()
    number = number.replace(/000$/, ' ')
    suffix = ` k${suffix}`
  }
  return <DeriNumberFormat value={number} isNumericString closeShowLoadingEffectAfterLoaded decimalScale={decimal} allowZero={allowZero} prefix={prefix} suffix={` ${suffix}`} />
}


function Position({ wallet, lang, trading, getLang }) {
  const [shareSymbol, setShareSymbol] = useState('')
  const [shareDirection, setShareDirection] = useState('')
  const [sharePnl, setSharePnl] = useState('')
  const [loadShare, setLoadShare] = useState(false)
  const [bodyWidth, setBodyWidth] = useState()
  const location = useLocation();
  const history = useHistory()
  const alert = useAlert();
  const toBeClosePosition = useRef()
  const headerRef = useRef();
  const trade = location.pathname.split('/')[2]
  const [positions, setPositions] = useStateWithCallback([])
  const [fixedColumn, setFixedColumn] = useState(false)
  const fixedColumnRef = useRef()

  const containerClass = classNames('position-container', {
    fixed: fixedColumn,
  })

  const showLoading = () => {
    show(<Loading />, {
      key: "loading",
      fading: true,
      style: {
        background: "rgba(0, 0, 0, 0.4)",
        zIndex: 2,
      }
    })
  }
  let columns = {
    symbol: { text: lang['symbol'], value: pos => pos['formatedSymbol'], className: 'symbol', onClick: (position) => onSelect(position.displaySymbol, position.b0Unit, position.bTokenLength === 1) },
    direction: { text: lang['direction'], className: pos => `${pos.directionColor}`, value: pos => <div className='strike'>{pos.direction}</div> },
    position: { text: lang['position'], value: pos => <><div className='strike'>{formatNumber(pos['displayVolume'], -1, pos.symbolUnit)}</div><div className='strike gray'>{formatNumber(pos['notional'], 2, pos.displayNotionalUnit)}</div></> },
    averageEntryPrice: { text: lang['entry-price'], value: pos => formatNumber(pos['averageEntryPrice'], trading.symbolInfo.getDecimalBySymbol && trading.symbolInfo.getDecimalBySymbol(pos.symbol)) },
    markPrice: { text: lang['mark-price'], value: pos => formatNumber(pos['markPrice'], trading.symbolInfo.getDecimalBySymbol && trading.symbolInfo.getDecimalBySymbol(pos.symbol)) },
    marginHeldBySymbol: { text: lang['margin-usage'], tip: lang['margin-usage-for-position'], value: pos => formatNumber(pos['marginHeldBySymbol'], 2) },
    // liquidationPrice: { text: lang['liq-Price'], tip: lang['liq-price-est'], value: pos => formatNumber(pos['liquidationPrice'], 2,'','',true) },
    unrealizedPnl: {
      text: lang['unrealized-pnl'], className: pos => (+pos['unrealizedPnl']).toFixed(2) > 0 ? 'green unrealizedPnl' : (+pos['unrealizedPnl']).toFixed(2) < 0 ? 'red unrealizedPnl' : 'unrealizedPnl', value: pos => <>
        <div>
          <div>{formatNumber(Math.abs(pos['unrealizedPnl']), 2, '', compareTwo((+pos['unrealizedPnl']).toFixed(2), 0, '+', '-'), true)}</div>
        </div>
        <div><Icon token='scan' width='14' onClick={() => { twitterShare(pos) }} /></div></>, tip: lang['unrealized-pnl-dpmm-hover']
    },
    fundingFee: { text: lang['fundingFee'], className: pos => (+pos['fundingFee']).toFixed(2) > 0 ? 'green' : (+pos['fundingFee']).toFixed(2) < 0 ? 'red' : '', tip: lang['funding-fee-hover'], value: pos => formatNumber((Math.abs(pos['fundingFee'])), 2, '', compareTwo((+pos['fundingFee']).toFixed(2), 0, '+', '-'), true) },
    action: { text: lang['action'], value: pos => closeButton(pos, lang['close']), className: 'close-position' }
  }
  if (Type.isOption) {
    columns = {
      symbol: { text: lang['symbol'], value: pos => pos['displaySymbol'] || pos['symbol'], className: 'symbol', onClick: (position) => onSelect(position.displaySymbol) },
      direction: { text: lang['direction'], className: pos => pos.directionColor, value: pos => <div className='strike'>{pos.direction}</div> },
      position: { text: lang['position'], value: pos => <><div className='strike'>{formatNumber(pos['displayVolume'], -1, pos.symbolUnit)}</div><div className='strike gray'>{formatNumber(pos['notional'], 2, pos.displayNotionalUnit)}</div></> },
      averageEntryPrice: { text: lang['entry-price'], value: pos => formatNumber(pos['averageEntryPrice'], 2) },
      markPrice: { text: lang['mark-price'], value: pos => formatNumber(pos['markPrice'], 2) },
      marginHeldBySymbol: { text: lang['margin-usage'], tip: lang['margin-usage-for-position'], value: pos => formatNumber(pos['marginHeldBySymbol'], 2) },
      unrealizedPnl: {
        text: lang['unrealized-pnl'], className: pos => (+pos['unrealizedPnl']).toFixed(2) > 0 ? 'green unrealizedPnl' : (+pos['unrealizedPnl']).toFixed(2) < 0 ? 'red unrealizedPnl' : 'unrealizedPnl', value: pos => <>
          <div>
            <div>{formatNumber(Math.abs(pos['unrealizedPnl']), 2, '', compareTwo((+pos['unrealizedPnl']).toFixed(2), 0, '+', '-'), true)}</div>
          </div>
          <div><Icon token='scan' width='14' onClick={() => { twitterShare(pos) }} /></div>
        </>, tip: lang['unrealized-pnl-dpmm-hover']
      },
      fundingFee: { text: lang['fundingFee'], className: pos => (+pos['fundingFee']).toFixed(2) > 0 ? 'green' : (+pos['fundingFee']).toFixed(2) < 0 ? 'red' : '', tip: lang['funding-fee-hover'], value: pos => formatNumber(Math.abs(pos['fundingFee']), 2, '', compareTwo((+pos['fundingFee']).toFixed(2), 0, '+', '-'), true) },
      action: { text: lang['action'], value: pos => closeButton(pos, lang['close']), className: 'close-position' }
    }
  }
  const keys = Object.keys(columns)
  const twitterShare = async position => {
    setLoadShare(true)
    showLoading()
    let pnl = ((+position.unrealizedPnl / +position.marginHeldBySymbol) * 100).toFixed(2)
    setShareSymbol(position.displaySymbol)
    setShareDirection(position.direction)
    setSharePnl(pnl)
  }


  const onAccept = useCallback(() => {
    // trading.pause();
    const selected = toBeClosePosition.current
    const all = positions.map((p, index) => {
      if (p.symbol === selected.symbol) {
        p.selected = true
      }
      return p;
    })
    setPositions(all, () => {
      if (positions.find(p => p.selected)) {
        setTimeout(() => {
          setPositions(positions.filter(p => !p.selected))
        }, 1000)
      }
    })
  }, [positions])

  const onError = useCallback(() => {
    // trading.pause();
    setPositions(positions.map(p => {
      if (p.selected) {
        p.recover = true;
        delete p.selected;
      }
      return p
    }), () => {
      if (positions.find(p => p.recover)) {
        setTimeout(() => {
          setPositions(positions.map(p => {
            if (p.recover) {
              delete p.recover
            }
            return p;
          }))
        }, 1000)
      }
    })

  }, [positions])

  const closePosition = async (pos) => {
    const symbolParam = trading.symbolInfo.isAllV3 ? pos.symbol : pos.symbolId
    toBeClosePosition.current = pos
    trading.pause()
    const res = await ApiProxy.request('closePosition',
      [
        wallet.detail.chainId,
        trading.pool.address,
        wallet.detail.account,
        symbolParam
      ],
      {
        includeResponse: true,
        write: true,
        subject: firstLetterUppercase(lang['close-position']),
        onAccept: onAccept,
        title: {
          processing: pos.direction === "Short" ? lang['buy-order-processing'] : lang['sell-order-processing'],
          success: pos.direction === "Short" ? lang['buy-order-executed'] : lang['sell-order-executed'],
          error: pos.direction === "Short" ? lang['buy-order-failed'] : lang['sell-order-failed'],
        },
        content: {
          success: `${pos.direction === "Short" ? lang['buy'] : lang['sell']} ${Math.abs(Number(pos.displayVolume))} ${pos.displaySymbol} `,
          error: lang['transaction-failed'],
        }
      })
    if (res && res.success) {
      trading.refresh();
    }
    window.setTimeout(trading.resume(), 2000);
  }
  const closeButton = (pos, label, key) => {
    return <Button label={label} bgColor='#0A172B' key={key} defaultBorderColor='rgba(205,122,55,0.5)' outline outlineColor='rgb(205,122,55)' onClick={() => closePosition(pos)} width='60' height='32' />
  }

  const onSelect = (symbol, bTokenSymbol, isSingleBtoken) => {
    const info = trading.symbols.find(info => info.displaySymbol === symbol)
    const prefix = trading.pool.retired ? '/retired' : ''
    let url = `${prefix}/trade/${info.displayCategory.toLowerCase()}/${info.displaySymbol}`
    if (info.isV2 && info.isFuture) {
      url = `${url}/${info.bTokenSymbol}`
    }
    if (isSingleBtoken) {
      url = `${url}/${bTokenSymbol}`
    }
    history.push(url)
    trading.setCookie(info.symbol, info.category, info.bTokenSymbol)
  }

  const resizeHandler = () => {
    if (headerRef.current) {
      const width = headerRef.current.scrollWidth;
      setBodyWidth(width)
    }
    if (document.querySelector('.m-position').scrollWidth > document.querySelector('.m-position').clientWidth) {
      setFixedColumn(true)
    } else {
      setFixedColumn(false)
    }
  }

  const onScroll = (event) => {
    const mainBody = document.querySelector('.m-position .p-body .main');
    mainBody.scrollTop = event.srcElement.scrollTop
  }

  useEffect(() => {
    setPositions(trading.positions)
    resizeHandler();
  }, [trading.positions])


  useEffect(() => {
    window.addEventListener('resize', resizeHandler)
    return () => window.removeEventListener('resize', resizeHandler)
  }, [])

  useEffect(() => {
    if (fixedColumnRef.current) {
      const mainBody = document.querySelector('.m-position .p-body .main');
      mainBody.onscroll = event => {
        fixedColumnRef.current.scrollTop = event.srcElement.scrollTop
      }
    }
  }, []);


  return (
    <div className={containerClass}>
      <div className='m-position'>
        {loadShare && <TwiterShare positions={positions} shareDirection={shareDirection} alert={alert} sharePnl={sharePnl} shareSymbol={shareSymbol} trade={trade} wallet={wallet} setSharePnl={setSharePnl} />}
        <div className='p-header' ref={headerRef} style={{ width: `${bodyWidth}px` }}>
          <GridHeader columns={columns} keys={keys} className='p-col' />
        </div>
        <div className='p-body' style={{ width: `${bodyWidth}px` }} >
          <TradeMaskHint postion="position" />
          <div className='main'>
            <PositionList loaded={trading.loaded} positions={positions} onSelect={onSelect} twitterShare={twitterShare} keys={keys} columns={columns} lang={lang} />
          </div>
        </div>
        {positions.length > 0 && <div className='p-footer'>
          <span className='liq-label'>
            <UnderlineText multiline tip={lang["option-liq-price-hover"]} text={lang['liq-Price']} />
          </span>
          <span >
            <LiqPriceDisplay pos={positions} lang={lang} />
          </span>
        </div>}
      </div>
      {trading.loaded && <FixedColumn data={positions} onScroll={onScroll} ref={fixedColumnRef} renderFixedElement={(p) => closeButton(p, lang['close'], 'fixedColumn')} selector='.m-position' headerLabel={lang['action']} />}
    </div>

  )
}


const PositionList = ({ loaded, positions, keys, columns, lang }) => {
  return (
    loaded ?
      positions.length > 0 ?
        positions.map((position, i) => {
          const rowClass = classNames('p-row', {
            selected: position.selected,
            recover: position.recover
          })
          return (
            <div key={i} >
              {position.recover && <div className='trans-error-tip'><Icon token='trans-failed-warn' /> {lang['transaction-failed']}</div>}
              <div className={rowClass} key={position.symbol}>
                {keys.map(key => {
                  const col = columns[key]
                  const colClass = classNames('p-col', typeof col.className === 'function' ? col.className(position) : col.className)
                  return (
                    <div className={colClass} key={key} onClick={() => typeof col.onClick === 'function' && col.onClick(position)}>
                      {typeof col.value === 'function' ? col.value(position) : (position[key] || columns[key].value)}
                    </div>
                  )
                })}
              </div>
            </div>
          )
        })
        :
        <div className='no-data'><Icon token='no-data' width='48' height='48' />{lang['no-data']}</div>
      : <>
        <div className='p-row loading-line'></div><div className='p-row loading-line'></div><div className='p-row loading-line'></div><div className='p-row loading-line'></div><div className='p-row loading-line'></div>
      </>
  )
}


function TwiterShare({ positions, shareDirection, alert, trade, shareSymbol, sharePnl, wallet, setShareSymbol, setShareDirection, setSharePnl }) {

  const showShareModal = (url, trade, alert) => {
    show(<ShareModal url={url} trade={trade} alert={alert} />, {
      key: 'shareModal',
      fading: true,
      style: {
        background: "rgba(0, 0, 0, 0.4)",
        zIndex: 2,
      }
    })
  }

  useEffect(() => {
    if (shareSymbol && sharePnl && shareDirection) {
      let twitter = document.getElementsByClassName('share-position')[0]
      html2canvas(twitter, {
        allowTaint: false,
        useCORS: true,
      }).then(canvas => {
        var dataURL = canvas.toDataURL('image/jpeg', 1);
        var arr = dataURL.split(','),
          mime = arr[0].match(/:(.*?);/)[1],
          bstr = atob(arr[1]),
          n = bstr.length,
          u8arr = new Uint8Array(n);
        while (n--) {
          u8arr[n] = bstr.charCodeAt(n);
        }
        let blob = new Blob([u8arr], { type: mime });
        let file = new window.File([blob], 'thumbnail.jpg', { type: 'image/jpeg' });
        let fd = new FormData();
        fd.append('file', file)
        let AxiosUrl = 'https://share.deri.io/upload_images'
        axios.post(AxiosUrl, fd).then(res => {
          let shareImg = res.data.data.url
          let img = new Image();
          img.src = shareImg
          img.onload = () => {
            showShareModal(shareImg, trade, alert)
            setSharePnl("")
            hide('loading')
          }
        }).catch(err => {
          hide('loading')
          alert.error('Network Failed')
        })
      })
    }
  }, [shareSymbol, sharePnl, shareDirection])
  return (
    <div style={{ position: 'fixed', height: 20, overflow: 'auto' }}>
      {positions.length > 0 && <div className={`share-position ${trade} ${shareSymbol === 'mBTC^2' || shareSymbol === 'mETH^2' ? "power" : "futures"}`} >
        <div className={` direction ${shareDirection}`}>
          {shareDirection.toLocaleUpperCase()}
        </div>
        <div className='symbol-name'>
          {shareSymbol}
        </div>
        <div className='my-pnl'>
          {sharePnl} %
        </div>
        <div className='my-address'>
          {formatAddress(wallet.detail.account)}
        </div>
      </div>}
    </div>
  )
}

export default inject('trading', 'wallet')(observer(Position))