
import UnderlineText from '../../../../components/UnderlineText/UnderlineText';

export default function GridHeader({ keys, columns ,className}) {
  return (
    keys.map(key => {
      return (
        <div key={key} className={className}>
          {columns[key].tip
            ?
            <UnderlineText tip={columns[key].tip} text={columns[key].text} multiline width='auto' />
            :
            columns[key].text
          }
        </div>
      )
    })
  )
}