import styled from "styled-components"
import Button from "../../../components/Button/Button"
import closeModalIcon from '../../../assets/img/close-modal-icon.svg'
import { hide } from "react-functional-modal"
import './confirmation.less'
import DeriNumberFormat from "../../../utils/DeriNumberFormat"
import { storeSkipConfirmation } from "../../../utils/utils"
import Label from "../../../components/Label/Label"
import { BUY } from "../../../utils/Constants"
import Type from "../../../model/Type"
import classNames from "classnames"
import Icon from "../../../components/Icon/Icon"
import Intl from '../../../model/Intl'

export default function Confirmation({ trading, lang, direction, wallet, alert, skipConfirmation, subject }) {
  const buttonText = (direction === BUY) ? lang['buy-long'] : lang['sell-short']
  const onClick = async () => {
    const res = await trading.doTransaction(function accept(){
      hide('confirmation')      
    })    
    if(res && res.success){
      storeSkipConfirmation(skipConfirmation)     
    }
  }
  return (
    <div className='confirmation'>
      <div className='confirm-title'>{lang['confirmation']} <img src={closeModalIcon} alt='' onClick={() => hide('confirmation')} /></div>
      <div className='confirm-body'>
        <div className={classNames('confirm-legend', trading.directionChanged.className)}>
          <div className='before-direction-volume'>
            <div className='before'><Label text={`${trading.directionChanged.before} ${trading.volumeChanged.before}`} className={trading.directionChanged.before} /></div>
            {/* <div className='before'><Label text={`${trading.volumeChanged.before}`} className={trading.directionChanged.before}/></div> */}
          </div>
          <div className='middle-arrow'>
            <div className='arrow'><Icon token='to-left' width='16' height='16' /></div>
            {/* <div className='arrow'>-></div> */}
          </div>
          <div className='now-direction-volume'>
            <div className='now'><Label text={`${trading.directionChanged.now} ${trading.volumeChanged.now}`} className={trading.directionChanged.now} /></div>
            {/* <div className='now'><Label text={`${trading.volumeChanged.now}`}  className={trading.directionChanged.now}/></div> */}
          </div>

        </div>
        <div className='confirm-row'>
          <div className='confirm-col'>{lang['volume']}</div>
          <div className='confirm-col value'><DeriNumberFormat value={trading.volumeChanged.totalVolume} suffix={` ${trading.symbolInfo.unit}`} decimalScale={trading.minTradeScaleDecimal} /></div>
        </div>
        <div className='confirm-row'>
          <div className='confirm-col'>{lang['trade-price']}</div>
          <div className='confirm-col value'><DeriNumberFormat value={trading.impactPreview.tradePrice} decimalScale={trading.symbolInfo.decimals || 2} /></div>
        </div>
        <div className='confirm-row'>
          <div className='confirm-col'>{lang['fee']}</div>
          <div className='confirm-col value'><DeriNumberFormat value={trading.impactPreview.fee} decimalScale={2} suffix={` ${trading.symbolInfo.bTokenSymbol}`} /></div>
        </div>
        <div className='confirm-row'>
          <div className='confirm-col'>{lang['margin-usage']}</div>
          <div className='confirm-col value'><DeriNumberFormat value={trading.impactPreview.marginUsage} decimalScale={2} />&nbsp;{"->"}&nbsp;<DeriNumberFormat value={trading.impactPreview.marginUsageAfter} allowZero decimalScale={2} /></div>
        </div>
        <div className='confirm-row'>
          <div className='confirm-col'>{lang['available-margin']}</div>
          <div className='confirm-col value'><DeriNumberFormat value={trading.impactPreview.availableMargin} decimalScale={2} />&nbsp;{"->"}&nbsp;<DeriNumberFormat value={trading.impactPreview.availableMarginAfter} allowZero decimalScale={2} /></div>
        </div>
        {Type.isFuture && <div className='confirm-row'>
          <div className='confirm-col'>{lang['liq-price']}</div>
          <div className='confirm-col value'><DeriNumberFormat value={trading.impactPreview.liqPrice} decimalScale={2} />&nbsp;{"->"}&nbsp;<DeriNumberFormat value={trading.impactPreview.liqPriceAfter} decimalScale={2} /></div>
        </div>}
      </div>
      <div className='confirm-btn'>
        {/* <TransactionButton  wallet={wallet} pool={trading.pool} subject={subject} lang={lang} ignoreApprove={true} width='323' height='54' fontColor='#FFFFFF' onClick={onClick} label={buttonText} fontSize='18' fontWeight='700'  /> */}
        <Button width='323' height='54' fontColor='#FFFFFF' onClick={onClick} label={buttonText} fontSize='18' fontWeight='700' />
      </div>
    </div>
  )
}