import { hide } from 'react-functional-modal'
import { useState } from 'react'
import Icon from '../Icon/Icon'
import { isBrowser } from 'react-device-detect'
import close from '../../assets/img/close-modal-icon.svg'
import shareBtn from './img/share-twitter.svg'
import { useLocation } from 'react-router-dom'
import './shareModal.less'
export default function ShareModal({ url,trade,alert }) {
  const [isCopy, setIsCopy] = useState(false)
  const onCopy = url => {
    if (url) {
      navigator.clipboard.writeText(url);
      alert.success('URL copied.')
      setIsCopy(true)
      let timeOut = window.setTimeout(()=>{
        setIsCopy(false)
      },1000)
    }
  }
  const shareTwitter = () => {
    if (url) {
      if (isBrowser) {
        window.open(`https://twitter.com/intent/tweet?url=${encodeURIComponent(`https://share.deri.io/?title=Deri,%20Your%20Option,%20Your%20Future!&description=&image=${url}&target=${encodeURIComponent(`https://deri.io/#/trade/${trade}`)}`)}`)
      } else {
        window.location.href = `https://twitter.com/intent/tweet?url=${encodeURIComponent(`https://share.deri.io/?title=Deri,%20Your%20Option,%20Your%20Future!&description=&image=${url}&target=${encodeURIComponent(`https://deri.io/#/trade/${trade}`)}`)}`
      }
    }

  }
  return (
    <div className='share-modal'>
      <div className='share-img'>
        <img src={url} />
      </div>
      <div className='share-btn'>
        <div className='close-share'>
          <img src={close} alt='' onClick={() => hide('shareModal')} />
        </div>
        <div className='copy-twitter'>
          <img src={shareBtn} alt='' className='share-twitter' onClick={shareTwitter} />
          <Icon token={isCopy ? "copy-s" : "copy"} width='20' onClick={() => onCopy(url)} />
        </div>
      </div>
    </div>
  )
}